.text-content {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: justify;
  color: #1b1f27;
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 20px;
      position: relative;
      padding-left: 35px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) ".";
        position: absolute;
        left: 10px;
        top: 0;
        color: #b89a6e;
        font-weight: 700;
      }
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 10px;
      position: relative;
      padding-left: 35px;
      &::before {
        content: "";
        position: absolute;
        left: 10px;
        top: 9px;
        width: 6px;
        height: 6px;
        background: #b89a6e;
        border-radius: 50%;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 0 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    letter-spacing: -0.02em;
    margin: 0 0 20px;
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    margin: 0 0 20px;
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-weight: 700;
    font-size: 30px;
    line-height: 38px;
    margin: 0 0 20px;
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    margin: 0 0 20px;
    &:first-child {
      margin-top: 0;
    }
  }
  img {
    display: inline-block;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: #2f6fed;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  hr {
    border-color: #d0d3d8;
    border-top: 0;
    margin: 20px 0;
  }
}
