.sign-up {
  position: relative;
  background: radial-gradient(89.62% 89.62% at 20% 20%, #ffe5c0 0%, #e9e9e9 100%);
  border: 1px solid #e9c795;
  padding: 20px;
  &__title {
    position: relative;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    color: #9d825c;
    padding-left: 2px;
    padding-right: 2px;
  }
  &__text {
    position: relative;
    margin: 0 0 30px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #1b1f27;
  }
  &__form {
    position: relative;
  }
  &__form-row {
    position: relative;
    margin-bottom: 10px;
  }
  &__form-row-label {
    position: relative;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1b1f27;
  }
  &__form-input {
    width: 100%;
    padding: 8px 10px;
    background: #fff;
    border: 1px solid #6e737d;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #6e737d;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #e9c795;
    }
    &:focus {
      border-color: #e9c795;
    }
    &::placeholder {
      font-style: italic;
      color: #959aa4;
    }
  }
  &__form-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 12px 24px;
    background: #1b1f27;
    margin-top: 20px;
    width: 100%;
    border: 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #fff;
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #e9c795;
      color: #23272f;
      svg {
        fill: #23272f;
      }
    }
    svg {
      fill: #fff;
      flex: 0 0 auto;
      margin-left: 12px;
      transition: .3s ease-in-out;
    }
  }
}
