.paginations {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  li {
    flex: 0 0 auto;
    margin-left: 5px;
    margin-right: 5px;
    @include response(1200px) {
      margin-left: 3px;
      margin-right: 3px;
    }
  }
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #1b1f27;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: 30px;
      height: 30px;
    }
    @include desktop-hover() {
      background: #1b1f27;
      color: #fff;
      svg {
        fill: #fff;
      }
    }
    svg {
      transition: .3s ease-in-out;
    }
  }
  span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #1b1f27;
    &.paginations__current {
      background: #1b1f27;
      color: #fff;
    }
  }
}
