.category {
  position: relative;
  &__title {
    position: relative;
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    color: #1b1f27;
    @include response(1200px) {
      font-size: 30px;
      line-height: 38px;
    }
  }
  &__text {
    position: relative;
    margin: 0 0 40px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: justify;
    color: #3f444d;
    @include response(1200px) {
      margin-bottom: 80px;
    }
  }
  &__alphabet {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -6px;
    margin-bottom: 14px;
    @include response(1200px) {
      max-width: 100vw;
      flex-wrap: nowrap;
      margin-left: -20px;
      margin-right: -20px;
      overflow-x: auto;
      padding-left: 20px;
      padding-right: 20px;
      margin-bottom: 40px;
    }
  }
  &__alphabet-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 44px;
    width: 44px;
    height: 44px;
    border: 2px solid #1b1f27;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #1b1f27;
    text-decoration: none;
    margin-left: 6px;
    margin-bottom: 6px;
    text-transform: uppercase;
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      margin-left: 0;
      margin-right: 6px;
      margin-bottom: 0;
    }
    @include desktop-hover() {
      background: #1b1f27;
      color: #fff;
      transform: translateY(-2px);
    }
    &.active {
      background: #1b1f27;
      color: #fff;
    }
  }
  &__list {
    position: relative;
  }
  &__row {
    position: relative;
    margin-bottom: 50px;
    display: flex;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
  &__row-title {
    position: relative;
    flex: 0 0 120px;
    width: 120px;
    text-align: center;
    margin-right: 10px;
    font-weight: 700;
    font-size: 60px;
    line-height: 72px;
    letter-spacing: -0.02em;
    color: #1b1f27;
    text-transform: uppercase;
    @include response(1200px) {
      flex: 0 0 65px;
      width: 65px;
    }
  }
  &__item {
    position: relative;
  }
  &__item-list-link {
    position: relative;
    display: flex;
    margin-left: -10px;
    flex-wrap: wrap;
  }
  &__item-mobile-btn {
    display: none;
    @include response(1200px) {
      align-items: center;
      justify-content: center;
      padding: 5px 10px;
      background: #f1ddc0;
      border: 1px solid #b89a6e;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #23272f;
      .category__item.hide-items & {
        display: flex;
      }
      .category__item.show-hide-items & {
        svg {
          transform: scaleY(-1);
        }
        span {
          &:nth-child(1) {
            display: none;
          }
          &:nth-child(2) {
            display: block;
          }
        }
      }
      span {
        &:nth-child(2) {
          display: none;
        }
      }
      svg {
        flex: 0 0 auto;
        margin-left: 8px;
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__item-link {
    position: relative;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #1b1f27;
    margin-bottom: 10px;
    margin-left: 10px;
    flex: 0 0 calc(33.333% - 10px);
    max-width: calc(33.333% - 10px);
    width: 100%;
    padding: 10px;
    background: #f9f6f1;
    text-align: center;
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      flex: 0 0 calc(50% - 10px);
      max-width: calc(50% - 10px);
      &:nth-child(12) ~ .category__item-link {
        display: none;
        .category__item.show-hide-items & {
          display: block;
        }
      }
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 10px);
      max-width: calc(100% - 10px);
    }
    @include desktop-hover() {
      background: #1b1f27;
      color: #fff;
      transform: translateY(-2px);
    }
  }
}
