.footer {
  position: relative;
  flex: 0 0 auto;
  background: #23272f;
  padding-top: 20px;
  padding-bottom: 20px;
  @include response(1200px) {
    padding: 40px 20px 50px;
    background: #0b1014;
  }
  &__container {
    position: relative;
    max-width: 1300px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__cell {
    flex: 0 0 35%;
    max-width: 35%;
    width: 100%;
    display: flex;
    @include response(1200px) {
      order: 1;
      flex: 0 0 auto;
      max-width: 100%;
      justify-content: center;
      margin-bottom: 20px;
    }
    &:last-child {
      justify-content: flex-end;
      @include response(1200px) {
        order: 2;
        margin-bottom: 0;
        justify-content: center;
      }
    }
  }
  &__logo {
    position: relative;
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__copy {
    flex: 0 0 30%;
    max-width: 30%;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #d0d3d8;
    text-align: center;
    @include response(1200px) {
      order: 3;
      flex: 0 0 auto;
      max-width: 100%;
      margin-top: 10px;
    }
  }
  &__links {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    li {
      margin-right: 23px;
      &:last-child {
        margin-right: 0;
        a {
          &::before {
            display: none;
          }
        }
      }
    }
    a {
      display: block;
      text-decoration: none;
      position: relative;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      color: #fff;
      @include desktop-hover() {
        text-decoration: underline;
      }
      &::before {
        content: "";
        position: absolute;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        background: #e9c795;
        top: 50%;
        margin-top: -1px;
        right: -13px;
        pointer-events: none;
      }
    }
  }
}
