.sources {
  position: relative;
  padding-top: 30px;
  border-top: 1px solid #d0d3d8;
  border-bottom: 1px solid #d0d3d8;
  padding-bottom: 30px;
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #3f444d;
  }
  &__head-btn {
    position: relative;
    flex: 0 0 auto;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #1b1f27;
    display: flex;
    align-items: center;
    span {
      &:nth-child(2) {
        display: none;
      }
    }
    .sources.hide & {
      span {
        &:first-child {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
  &__head-btn-icon {
    position: relative;
    background: #f1ddc0;
    border-radius: 50%;
    flex: 0 0 28px;
    width: 28px;
    height: 28px;
    margin-left: 10px;
    &::before {
      content: "";
      position: absolute;
      background: #e02d3c;
      width: 10px;
      height: 2px;
      left: 50%;
      margin-left: -5px;
      top: 50%;
      margin-top: -1px;
    }
    &::after {
      content: "";
      position: absolute;
      background: #e02d3c;
      height: 10px;
      width: 2px;
      left: 50%;
      margin-top: -5px;
      top: 50%;
      margin-left: -1px;
      opacity: 0;
      transition: 0.3s ease-in-out;
    }
    .sources.hide & {
      &::after {
        opacity: 1;
      }
    }
  }
  &__list {
    position: relative;
    padding: 34px 0 0;
    list-style: none;
    margin: 0;
    counter-reset: myCounter;
    .sources.hide & {
      display: none;
    }
  }
  &__item {
    position: relative;
    margin: 0 0 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #0b1014;
    padding-left: 25px;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      counter-increment: myCounter;
      content: counter(myCounter) ".";
    }
    a {
      display: inline-block;
      vertical-align: top;
      color: #2f6fed;
      text-decoration: none;
      @include response(1200px) {
        max-width: calc(100vw - 65px);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
}
