.top-btn-box {
  position: relative;
  display: flex;
  justify-content: flex-end;
  margin-top: 44px;
  margin-right: -50px;
  @include response(1200px) {
    display: none;
  }
  &--mobile {
    display: none;
    @include response(1200px) {
      display: flex;
      justify-content: center;
      margin-right: 0;
      width: 100%;
      margin-top: 0;
    }
  }
}
.top-btn {
  position: relative;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  background: #fff;
  border: 2px solid #1b1f27;
  cursor: pointer;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #1b1f27;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    background: #e9c795;
    border-color: #e9c795;
    .top-btn__icon {
      transform: translateY(-2px);
    }
  }
  &__icon {
    position: relative;
    flex: 0 0 auto;
    margin-right: 10px;
    transition: 0.2s ease-in-out;
  }
}
