.first-screen {
  position: relative;
  background: #f9f6f1;
  padding-top: 80px;
  padding-bottom: 48px;
  @include response(1200px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__title {
    position: relative;
    margin: 0 0 20px;
    font-weight: 600;
    font-size: 60px;
    line-height: 72px;
    text-align: center;
    letter-spacing: -0.02em;
    color: #1b1f27;
    @include response(1200px) {
      font-size: 30px;
      line-height: 38px;
    }
  }
  &__descriptions {
    position: relative;
    max-width: 748px;
    width: 100%;
    margin: 0 auto 40px;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    color: #3f444d;
    @include response(1200px) {
      font-size: 16px;
      line-height: 24px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__search {
    &::before {
      content: "";
      position: absolute;
      bottom: -48px;
      left: 50%;
      width: 100vw;
      margin-left: -50vw;
      height: 84px;
      background: #fff;
    }
  }
  &__search-form {
    position: relative;
    max-width: 620px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    &::before {
      content: "";
      position: absolute;
      left: 20px;
      right: 20px;
      height: 8px;
      background: #1b1f27;
      top: 100%;
      @include response(1200px) {
        height: 4px;
      }
    }
  }
  &__search-icon {
    position: absolute;
    left: 28px;
    top: 50%;
    margin-top: -20px;
    fill: #1b1f27;
    @include response(1200px) {
      width: 18px;
      height: 18px;
      margin-top: -9px;
      left: 15px;
    }
  }
  &__search-input {
    width: 100%;
    padding: 10px 24px 10px 82px;
    height: 68px;
    background: #fff;
    border: 1px solid #1b1f27;
    font-weight: 400;
    font-size: 30px;
    color: #1b1f27;
    @include response(1200px) {
      height: 50px;
      font-size: 24px;
      padding-left: 40px;
    }
    &::placeholder {
      color: #959aa4;
    }
  }
}
