.share {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  li {
    flex: 0 0 auto;
    margin-right: 10px;
  }
  button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1b1f27;
    background: #fff;
    border: 0;
    cursor: pointer;
    transition: .2s ease-in-out;
    @include desktop-hover() {
      transform: translateY(-2px);
    }
    img {
      flex: 0 0 24px;
      margin-right: 4px;
    }
  }
}
