.atricle {
  position: relative;
  &__title {
    margin: 0 0 10px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    color: #1b1f27;
    @include response(1200px) {
      font-size: 26px;
      line-height: 32px;
    }
  }
  &__share {
    position: relative;
    border-bottom: 1px solid #D0D3D8;
    margin-bottom: 40px;
    padding-bottom: 10px;
    @include response(1200px) {
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      overflow-x: auto;
    }
  }
  &__content {
    position: relative;
  }
}

