.survey {
  position: relative;
  padding-top: 100px;
  @include response(1200px) {
    padding-top: 50px;
  }
  &__title {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #1b1f27;
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  &__btn {
    position: relative;
    flex: 0 0 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #fff;
    margin-left: 5px;
    margin-right: 5px;
    padding: 6px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    @include desktop-hover() {
      transform: translateY(-2px);
    }
    svg {
      flex: 0 0 auto;
      margin-right: 8px;
    }
    &--green {
      background: #009e6a;
    }
    &--red {
      background: #e02d3c;
    }
  }
  &__text {
    position: relative;
    text-align: center;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1b1f27;
  }
  &__scale {
    position: relative;
    width: 210px;
    margin-left: auto;
    margin-right: auto;
    height: 7px;
    background: #e02d3c;
    overflow: hidden;
  }
  &__scale-inner {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background: #009e6a;
  }
}
