.mobile-link {
  display: none;
  @include response(1200px) {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex: 0 0 auto;
    padding: 8px 32px;
    background: #f9f6f1;
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #23272f;
    transition: 0.3s ease-in-out;
    margin-bottom: 30px;
    margin-top: 10px;
  }
  svg {
    flex: 0 0 auto;
    margin-right: 10px;
  }
}
