.wpcf7-not-valid-tip {
  position: absolute !important;
  left: 0 !important;
  right: 0 !important;
  top: 100%;
  margin: 2px 0 0 !important;
  color: #ff5e2b !important;
  font-size: 10px !important;
}

.wpcf7-form-control-wrap {
  display: block;
}

.wpcf7 form .wpcf7-response-output {
  display: none !important;
}

.wpcf7-form-control-wrap {
  position: relative;
  display: block;
}
.wpcf7-form-control-wrap span {
  display: block;
  position: relative;
  margin: 0;
}
.wpcf7-form-control-wrap label {
  display: block;
}
.wpcf7-form-control-wrap label input {
  display: none;
}
.wpcf7-form-control-wrap label input:checked + span::after {
  opacity: 1;
}
.wpcf7-form-control-wrap label span {
  position: relative;
  display: block;
  padding-left: 25px;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #9b9b9b;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.wpcf7-form-control-wrap label span::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 1px;
  width: 12px;
  height: 12px;
  background: #fff;
}
.wpcf7-form-control-wrap label span::after {
  content: "";
  position: absolute;
  left: 5px;
  top: 3px;
  width: 8px;
  height: 5px;
  border-left: 2px solid #000;
  border-bottom: 2px solid #000;
  transform: rotate(-45deg);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.wpcf7-form-control-wrap label span a {
  color: #9b9b9b;
}
@media only screen and (min-width: 1025px) {
  .wpcf7-form-control-wrap label span a:hover {
    text-decoration: none;
  }
}

