.mentioned {
  position: relative;
  margin-bottom: 30px;
  &__title {
    position: relative;
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #3f444d;
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0 0 0 -10px;
    li {
      position: relative;
      flex: 0 0 calc(33.333% - 10px);
      max-width: calc(33.333% - 10px);
      width: 100%;
      margin-left: 10px;
      margin-bottom: 10px;
      @include response(1200px) {
        flex: 0 0 calc(50% - 10px);
        max-width: calc(50% - 10px);
      }
      @include response(767px) {
        flex: 0 0 calc(100% - 10px);
        max-width: calc(100% - 10px);
      }
    }
    a {
      display: block;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      img {
        width: 100%;
      }
      @include desktop-hover() {
        transform: translateY(-2px);
      }
    }
  }
  &__small {
    flex: 0 0 calc(25% - 10px);
    max-width: calc(25% - 10px);
    @include response(1200px) {
      flex: 0 0 calc(50% - 10px);
      max-width: calc(50% - 10px);
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 10px);
      max-width: calc(100% - 10px);
    }
  }
  &__item {
    display: block;
    padding: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #1b1f27;
    background: #f1ddc0;
    border-width: 1px;
    border-style: solid;
    border-color: #b89a6e;
    text-align: center;
  }
}
