.main {
  position: relative;
  max-width: 1440px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: flex-start;
  padding-bottom: 80px;
  @include response(1200px) {
    flex-direction: column-reverse;
    padding-bottom: 40px;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 320px;
    bottom: 80px;
    width: 1px;
    background: #1b1f27;
    @include response(1200px) {
      display: none;
    }
  }
  &__aside {
    position: sticky;
    top: 70px;
    padding: 20px;
    flex: 0 0 320px;
    max-width: 320px;
    width: 100%;
    @include response(1200px) {
      position: relative;
      flex: 0 0 auto;
      max-width: 100%;
      top: auto;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 20px;
        right: 20px;
        height: 1px;
        background: #23272f;
      }
    }
  }
  &__aside-icon {
    position: relative;
    margin-bottom: 12px;
  }
  &__aside-info {
    position: relative;
    padding-bottom: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1b1f27;
    margin: 0 0 20px;
    border-bottom: 1px solid #1b1f27;
  }
  &__aside-link {
    position: relative;
    display: block;
    text-decoration: none;
    padding: 5px 30px 5px 10px;
    background: #f1ddc0;
    color: #1b1f27;
    border: 1px solid #b89a6e;
    margin-bottom: 60px;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__aside-link-title {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }
  &__aside-link-text {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
  }
  &__aside-link-icon {
    position: absolute;
    top: 7px;
    right: 12px;
  }
  &__content {
    position: relative;
    flex: 1 1 auto;
    padding: 20px 120px 0 70px;
    @include response(1200px) {
      padding: 0 20px;
      margin-bottom: 60px;
    }
  }
}
