.main-categories {
  position: relative;
  padding: 40px 70px 20px;
  @include response(1200px) {
    padding: 40px 0;
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -60px;
    @include response(1200px) {
      margin-left: -20px;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(20% - 60px);
    max-width: calc(20% - 60px);
    width: 100%;
    margin-left: 60px;
    margin-bottom: 60px;
    color: #3f444d;
    text-decoration: none;
    @include response-min(1201px) {
      transition: color 0.3s ease-in-out;
    }
    @include response(1200px) {
      flex: 0 0 calc(50% - 20px);
      max-width: calc(50% - 20px);
      margin-left: 20px;
      margin-bottom: 30px;
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include desktop-hover() {
      color: #b89a6e;
    }
  }
  &__item-img {
    position: relative;
    margin-bottom: 10px;
    img {
      width: 100%;
    }
  }
  &__item-title {
    position: relative;
    display: block;
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
  }
}
