.breadcrumb {
  list-style: none;
  padding: 10px 70px;
  margin: -20px -140px 40px -70px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #373737;
  @include response(1200px) {
    margin: 0 -20px 30px;
    padding-left: 20px;
    padding-right: 20px;
  }
  li {
    position: relative;
    margin-right: 25px;
  }
  a {
    display: block;
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #0b1014;
    @include desktop-hover() {
      text-decoration: underline;
    }
    &::before {
      content: "";
      position: absolute;
      background: #0b1014;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      top: 50%;
      margin-top: -3px;
      right: -15px;
      pointer-events: none;
    }
  }
  span {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #0b1014;
  }
}
