.feedback {
  position: relative;
  margin-bottom: 20px;
  &__title {
    position: relative;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000;
  }
  &__btn {
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding: 6px 16px;
    border: 2px solid #1b1f27;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: #1b1f27;
    text-transform: uppercase;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #e9c795;
      border-color: #e9c795;
    }
  }
}