body {
  height: 100%;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  @include response(1200px) {
    ::-webkit-scrollbar {
      display: none;
    }
  }
}

.wrapper {
  position: relative;
  max-width: 100%;
  width: 100%;
  padding-top: 65px;
  flex: 1 1 auto;
  @include response(1200px) {
    padding-top: 53px;
  }
}

.container {
  position: relative;
  max-width: 1300px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1340px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
