.header {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  border-bottom: 1px solid #23272f;
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1300px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 auto;
  }
  &__logo {
    position: relative;
    @include response(1200px) {
      max-width: 200px;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      position: initial;
    }
  }
  &__categories {
    position: relative;
    flex: 0 0 auto;
    margin-left: 30px;
    @include response(1200px) {
      margin-left: 0;
      margin-bottom: 30px;
    }
  }
  &__categories-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  &__home-link {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex: 0 0 auto;
    margin-left: 20px;
    padding: 20px 32px;
    background: #f9f6f1;
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #23272f;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      margin-right: 20px;
      padding-top: 8px;
      padding-bottom: 8px;
    }
    @include desktop-hover() {
      color: #b89a6e;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__search {
    position: relative;
    flex: 0 0 360px;
    width: 360px;
    @include response(1200px) {
      flex: 0 0 auto;
      width: auto;
      position: initial;
    }
  }
  &__search-form {
    position: relative;
    @include response(1200px) {
      position: absolute;
      top: 100%;
      width: 100vw;
      left: 50%;
      margin-left: -50vw;
      transform: translateY(-300%);
      opacity: 0;
      visibility: hidden;
      transition: 0.3s ease-in-out;
      .header__search.active & {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
    }
  }
  &__search-icon {
    position: absolute;
    left: 12px;
    top: 50%;
    margin-top: -14px;
  }
  &__search-input {
    padding: 8px 10px 8px 52px;
    height: 48px;
    background: #fff;
    border: 1px solid #23272f;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    color: #23272f;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      border-left: 0;
      border-right: 0;
    }
    &:focus {
      border-color: #b89a6e;
    }
    &::placeholder {
      color: #959aa4;
      font-style: italic;
    }
  }
  &__search-mobile-btn {
    display: none;
    @include response(1200px) {
      display: block;
      position: relative;
      width: 30px;
      height: 30px;
      border: 1px solid #23272f;
    }
  }
  &__search-mobile-open {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -9px;
    transition: 0.3s ease-in-out;
    .header__search.active & {
      transform: scale(0);
      opacity: 0;
    }
  }
  &__search-mobile-close {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -9px;
    opacity: 0;
    transform: scale(0);
    transition: 0.3s ease-in-out;
    .header__search.active & {
      transform: scale(1);
      opacity: 1;
    }
  }
  &__mobile-menu {
    display: none;
    @include response(1200px) {
      display: block;
      position: relative;
      width: 30px;
      height: 30px;
      flex: 0 0 30px;
      background: #23272f;
      margin-left: 16px;
    }
  }
  &__mobile-menu-open {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -6px;
    transition: 0.3s ease-in-out;
    .header.active-menu & {
      transform: scale(0);
      opacity: 0;
    }
  }
  &__mobile-menu-close {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -9px;
    opacity: 0;
    transform: scale(0);
    transition: 0.3s ease-in-out;
    .header.active-menu & {
      transform: scale(1);
      opacity: 1;
    }
  }
  &__mobile {
    display: none;
    @include response(1200px) {
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      height: calc(100vh - 53px);
      opacity: 0;
      background: #fff;
      padding-bottom: 40px;
      overflow-y: auto;
      visibility: hidden;
      transform: translateY(-100vh);
      transition: 0.3s ease-in-out;
      .header.active-menu & {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.menu-item-has-children {
  position: relative;
  @include desktop-hover() {
    > span {
      &::before {
        transform: rotate(135deg);
        margin-top: -4px;
      }
    }
    > ul {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
  > span {
    display: block;
    position: relative;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #23272f;
    padding-right: 28px;
    padding: 20px 28px 20px 20px;
    cursor: pointer;
    @include response(1200px) {
      padding-bottom: 0;
    }
    &::before {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      border-bottom: 2px solid #23272f;
      border-left: 2px solid #23272f;
      transform: rotate(-45deg);
      right: 9px;
      top: 50%;
      margin-top: -8px;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        display: none;
      }
    }
  }
  > ul {
    position: absolute;
    list-style: none;
    padding: 0;
    margin: 0;
    top: 100%;
    left: 0;
    width: 240px;
    background: #fff;
    z-index: 1;
    padding: 10px 0px;
    border: 1px solid #23272f;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    opacity: 0;
    visibility: hidden;
    transform-origin: 50% 0;
    transform: translateY(20px);
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      position: relative;
      visibility: visible;
      transform: none;
      opacity: 1;
      width: 100%;
      border: 0;
      box-shadow: none;
    }
    li {
      position: relative;
    }
    a {
      display: flex;
      align-items: center;
      padding: 10px;
      text-decoration: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #3f444d;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        padding-left: 20px;
        padding-right: 20px;
      }
      @include desktop-hover() {
        color: #b89a6e;
      }
      img {
        width: 56px;
        flex: 0 0 56px;
        margin-right: 10px;
      }
    }
  }
}
