.search {
  position: relative;
  &__top-text {
    position: relative;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #3f444d;
    padding-top: 20px;
    @include response(1200px) {
      font-size: 18px;
    }
  }
  &__title-box {
    position: relative;
    margin-bottom: 60px;
    display: flex;
    align-items: center;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__title-icon {
    flex: 0 0 auto;
    margin-right: 13px;
    @include response(1200px) {
      margin-right: 10px;
      svg {
        width: 24px;
        height: 24px;
      }
    }
  }
  &__title {
    position: relative;
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    color: #1b1f27;
    @include response(1200px) {
      font-size: 24px;
      line-height: 30px;
    }
  }
  &__list {
    position: relative;
    margin-bottom: 40px;
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
  }
  &__item {
    position: relative;
    text-decoration: none;
    padding: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #1b1f27;
    background: #f9f6f1;
    flex: 0 0 calc(33.333% - 10px);
    max-width: calc(33.333% - 10px);
    width: 100%;
    margin-left: 10px;
    margin-bottom: 10px;
    text-align: center;
    transition: 0.2s ease-in-out;
    @include response(1200px) {
      flex: 0 0 calc(50% - 10px);
      max-width: calc(50% - 10px);
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 10px);
      max-width: calc(100% - 10px);
    }
    @include desktop-hover() {
      background: #1b1f27;
      color: #fff;
      transform: translateY(-2px);
    }
  }
}
