.table-content {
  position: relative;
  max-width: 320px;
  width: 100%;
  margin-bottom: 40px;
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    padding: 4px 13px;
    background: #fcfbf8;
    border: 1px solid #d0d3d8;
    cursor: pointer;
    .table-content.hide & {
      .table-content__head-arrow {
        transform: scaleY(-1);
      }
      .table-content__head-title {
        color: #b89a6e;
      }
      svg {
        fill: #b89a6e;
      }
    }
    @include desktop-hover() {
      .table-content__head-title {
        color: #b89a6e;
      }
      svg {
        fill: #b89a6e;
      }
    }
  }
  &__head-icon {
    flex: 0 0 auto;
    margin-right: 10px;
    fill: #23272f;
    transition: 0.3s ease-in-out;
  }
  &__head-title {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #23272f;
    transition: 0.3s ease-in-out;
  }
  &__head-arrow {
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right: 15px;
    transform: scaleY(1);
    transition: 0.3s ease-in-out;
    fill: #23272f;
  }
  &__body {
    position: relative;
    border: 1px solid #d0d3d8;
    border-top: 0;
    .table-content.hide & {
      display: none;
    }
  }
  &__list {
    list-style: none;
    padding: 10px 0;
    margin: 0;
    li {
      position: relative;
    }
    a {
      display: block;
      text-decoration: none;
      padding: 6px 20px;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #1b1f27;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: #b89a6e;
      }
    }
  }
  &__list-children {
    padding-left: 20px;
  }
}
